export const useAdmissionDashboardMaster = (args) => {
  const { data, refetch, isFetching } = useAdmissionDashboardMasterQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    admissionDashboardMaster: data,
    admissionDashboardMasterRefetch: refetch,
    admissionDashboardMasterLoading: isFetching,
  };
};

